<!--  -->
<template>
  <div class='container'>
    <el-drawer class="menuDrawer" title="" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'15%':'70%'">
      <el-form ref="Dateform" :model="DateLog" v-show="!isSearching">
        <el-form-item>
          <el-autocomplete placeholder="" v-model="DateLog.target" class="input-with-select" ref="targetInput" :fetch-suggestions="querySearch">
            <el-select class="targetSelect" v-model="DateLog.primary" slot="prepend" placeholder="">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
            </el-select>
          </el-autocomplete>
          <el-input placeholder="" v-model="DateLog.remark" clearable class="remarkInput" ref="remarkInput">
            <div class="remark" slot="prepend"> </div>
          </el-input>
          <el-button class="Inputbutton" v-show="!isEdit" type="primary" @click="addToList()"></el-button>
          <el-button class="Inputbutton" v-show="isEdit" type="success" @click="upDateToList()"></el-button>
          <el-button class="Inputbutton" type="primary" :disabled="isEdit" @click="importNow()"></el-button>
          <el-button class="Inputbutton" type="primary" :disabled="isEdit" @click="getDateRoute()"></el-button>
        </el-form-item>
        <el-form-item>
          <el-table :data="DateLog.targetList" style="width: 100%" height="260" :header-cell-style="{'text-align':'center'}" :row-class-name="tableRowClassName">

            <el-table-column label="" width="100%" align="center">
              <div slot-scope="scope">
                <!-- <i class="el-icon-time"></i> -->
                <span style="margin-left: 10px">{{ scope.row.place }}</span>
              </div>
            </el-table-column>

            <el-table-column label="" width="110%" align="center">
              <div slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.name }}</span>
              </div>
            </el-table-column>

            <el-table-column label="" align="center">
              <div slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" :disabled="isEdit"></el-button>
              </div>
            </el-table-column>
          </el-table>
        </el-form-item>
      </el-form>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content=""></el-page-header>
        <div class="routeContent">
          <span class="totoLength">{{""+length+""}}</span>
          <el-button class="followBtn" size="mini" type="primary"></el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: ["drawer"],
  data() {
    //
    return {
      mydrawer: this.drawer,
      isEdit: false,
      isSearching: false,
      currentIndex: 0,
      DateLog: {
        primary: 1,
        target: "",
        remark: "",
        targetList: [],
      },
      selectList: [],
      length: 0,
      numEvent: 0,
    };
  },
  // data
  computed: {},
  //data
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
    mydrawer: function (newVal, oldVal) {
      if (newVal === false) {
        this.$bus.$emit("cancelpick");
      }
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.selectList = this.loadAll();
  },
  // - 
  beforeCreate() {},
  // - 
  beforeMount() {},
  // - 
  beforeUpdate() {},
  // - 
  updated() {},
  // - 
  beforeDestroy() {},
  // - 
  destroyed() {},
  //keep-alive
  activated() {},
  //
  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (row.priority === 2) {
        return "warning-row";
      }
      return "";
    },
    addToList() {
      if (this.DateLog.target === "") {
        return;
      }
      var item = {
        priority: this.DateLog.primary,
        place: this.DateLog.target,
        name: this.DateLog.remark,
      };
      this.DateLog.targetList.push(item);
      this.DateLog.target === "";
      this.DateLog.remark === "";
      this.numEvent++;
    },

    handleEdit(index, row) {
      this.isEdit = true;
      this.currentIndex = index;
      this.DateLog.primary = row.priority;
      this.DateLog.target = row.target;
      this.DateLog.remark = row.name;
    },
    upDateToList() {
      this.DateLog.targetList[this.currentIndex].name = this.DateLog.remark;
      this.DateLog.targetList[this.currentIndex].target = this.DateLog.target;
      this.DateLog.targetList[this.currentIndex].priority =
        this.DateLog.primary;
      this.isEdit = false;
    },
    handleDelete(index, row) {
      this.DateLog.targetList = this.DateLog.targetList.filter((todo) => {
        return todo != row;
      });
      this.numEvent--;
    },

    getDateRoute() {
      if (this.numEvent < 2) {
        return;
      }
      this.isSearching = true;
    },
    goBack() {
      this.isSearching = false;
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      console.log(this.selectList);
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },

    // 
    importNow() {
      // json
      this.$message("...");
      fetch("/static/Plugins/Json/corse.json")
        .then((data) => data.json())
        .then((data) => {
          let res = [];
          for (let i = 0; i < data.data.length; i++) {
            let temp = data.data[i].list;
            if (temp.length > 0) {
              for (let j in temp) {
                res.push({
                  name: temp[j].name,
                  place: temp[j].place,
                  priority: temp[j].priority,
                });
              }
            }
          }

          setTimeout(() => {
            this.DateLog.targetList = res;

            this.$message({
              message: "!",
              type: "success",
            });
          }, 3000);
        });
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
  overflow: hidden;
}

.input-with-select {
  margin: 20px 10px;
  width: 90%;
}

.remarkInput {
  width: 90%;
  margin: 0 10px;
}
.remark {
  width: 50px;
}

.input-with-select .targetSelect {
  width: 90px;
  font-size: 14px;
}

.Inputbutton {
  float: right;
  margin-right: 10px;
  margin-top: 20px;
}

.getRoute {
  margin: 10px 10px;
}
.routeContent {
  position: relative;
  margin: 10px 20px;
}
.routeContent .followBtn {
  position: absolute;
  right: 10px;
}
</style>
<style >
.el-table .warning-row {
  background: oldlace;
}
</style>


