<!--  -->
<template>
    <div id="sys-setting" style="position:absolute;display: none;
    left: 73%;top: 1%; z-index: 10;">
        <el-card class="box-card" shadow="always">
            <div slot="header" class="clearfix">
                <span></span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onClose"></el-button>
            </div>
            <div class="text item">
                <el-tabs tab-position="left" style="height: 200px;">
                    <el-tab-pane label="">
                        <el-form label-width="100px">
                            <el-tooltip class="item" effect="light" content="" placement="bottom">
                                <el-form-item label="">
                                    <el-switch
                                        v-model="isAutoLocated"
                                        active-color="#13ce66"
                                        inactive-color="#ff4949">
                                    </el-switch>
                                </el-form-item>
                            </el-tooltip>
                        </el-form>
                    </el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                </el-tabs>
            </div>
        </el-card>
    </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
//import
components: {},
data() {
//
    return {
        isAutoLocated: this.$store.state.auto_orient
    };
},
// data
computed: {},
//data
watch: {},
//
methods: {
    onShow(){
        document.getElementById('sys-setting').style.display = 'block';
    },

    onClose(){
        document.getElementById('sys-setting').style.display = 'none';
    }
},
// - this
created() {

},
// - DOM
mounted() {

},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 480px;
    }
</style>


