<!--  -->
<template>
  <div id="ropute-plan" class='navigator-card' style="position:absolute;display: none;
left: 75%;top: 1%; z-index: 100;">
    <el-card class="box-card" shadow="always">
      <div slot="header" class="clearfix">
        <span></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="onClose"></el-button>
      </div>
      <div class="text item">
        <template v-if="!isSubmit">
          <el-row>
            <el-autocomplete class="inline-input" v-model="navigatorForm.origin" :fetch-suggestions="querySearch" :placeholder="originText" @select="handleSelect"></el-autocomplete>
            <el-autocomplete class="inline-input" v-model="navigatorForm.target" :fetch-suggestions="querySearch" :placeholder="targetText" @select="handleSelect"></el-autocomplete>
          </el-row>
          <el-row style="margin-top:15px;">
            <el-select v-model="PathOptions.value" placeholder="" style="width:202px;">
              <el-option v-for="item in PathOptions.options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
            <el-button type="primary" @click="onSubmit"></el-button>
          </el-row>
          <el-row>
            <el-table :data="ResultPathList" style="width: 100%" height="300">
              <el-table-column label="" width="360">
                <template slot-scope="scope">
                  <i class="el-icon-s-promotion"></i>
                  <span style="margin-left: 10px">{{ scope.row.place }}</span>
                </template>
              </el-table-column>
            </el-table>
          </el-row>
        </template>
      </div>
    </el-card>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

let pointer = null;

export default {
  //import
  components: {},
  data() {
    //
    return {
      originText: "",
      targetText: "",

      selectPathList: [],

      isSubmit: false,

      originText: "",
      targetText: "",

      navigatorForm: {
        origin: "",
        target: "",
        isWalk: true,
        isSignUp: true,
        isParkNeed: true,
      },

      roles: {
        origin: [{ required: true, message: "", trigger: "blur" }],
        target: [{ required: true, message: "", trigger: "blur" }],
      },

      PathOptions: {
        options: [],
        value: "",
      },

      ResultPathList: [
        {
          place: "1",
        },
        {
          place: "2",
        },
        {
          place: "3",
        },
        {
          place: "4",
        },
        {
          place: "5",
        },
      ],
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    init() {
      pointer = this;
    },

    querySearch(queryString, cb) {
      var list = this.selectPathList;
      var results = queryString
        ? list.filter(this.createFilter(queryString))
        : list;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (list) => {
        return (
          list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    loadAll() {
      // console.log(this.$store.state.placeList);
      return this.$store.state.placeList;
    },

    // 
    refresh() {
      this.navigatorForm.origin = "";
      this.navigatorForm.target = "";

      // 
      if (this.$store.state.auto_orient) {
        this.originText = "...";
        this.getLocation();
      } else {
        this.originText = "";
      }
    },

    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (data) => {
            // todo 
            console.log(data);
          },
          (error) => {
            console.log(error);
            // 
            pointer.origin = "";
          },
          {
            enableHighAccuracy: true,
            timeout: 3000,
          }
        );
      } else {
        this.$confirm(
          "''",
          "",
          {
            confirmButtonText: "",
            type: "error",
            center: true,
          }
        );
      }
    },

    onSubmit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          // todo
          this.$message("~");
        } else {
          // throw error
        }
      });
    },

    onShow() {
      this.refresh();
      document.getElementById("ropute-plan").style.display = "block";
    },

    onSubmit() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          // todo
          this.isSubmit = true;
          // this.$message('~');
        } else {
          // throw error
        }
      });
    },

    onShow() {
      this.refresh();
      document.getElementById("ropute-plan").style.display = "block";
    },

    onClose() {
      this.isSubmit = false;
      document.getElementById("ropute-plan").style.display = "none";
    },

    back() {
      this.isSubmit = false;
    },

    handleSelect(item) {
      console.log(item);
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.selectPathList = this.loadAll();
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/*  */
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 16px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 450px;
}
</style>


