<!--  -->
<template>
<div id="sys-about" style="position: absolute; display:none">
    <el-dialog
        center
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span>v1.0,:</span><br>
        <span>114192 </span><br>
        <span>114193 </span><br>
        <span>114193 </span><br>
        <span>114193 </span><br>
        <span>114192 </span><br>
        <span slot="footer" class="dialog-footer">
            3S
        </span>
    </el-dialog>
</div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
//import
components: {},
data() {
    //
    return {
        dialogVisible:false,
    };
},
// data
computed: {},
//data
watch: {},
//
methods: {
    onShow(){
        document.getElementById('sys-about').style.display = 'block';
        this.dialogVisible = true;
    },

    handleClose(done){
        document.getElementById('sys-about').style.display = 'none';
        done();
    }
},
// - this
created() {

},
// - DOM
mounted() {

},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style scoped>

</style>


