<!--  -->
<template>
    <div id="running-helper" class='running-helper' style="position:absolute;display: none;
    left: 77%;top: 1%; z-index: 100;">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{title}}</span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onClose"></el-button>
            </div>
            <div class="text item">
                <template v-if="!isSubmit">
                    <el-form ref="running_form" :model="RunningForm" label-width="80px">
                        <el-form-item label="" prop="origin">
                            <el-autocomplete
                            class="inline-input"
                            v-model="RunningForm.origin"
                            :fetch-suggestions="querySearch"
                            placeholder=""
                            @select="handleSelect"
                            ></el-autocomplete>
                            <!-- <el-input v-model="RunningForm.origin" placeholder=""></el-input> -->
                        </el-form-item>
                        <el-form-item label="" prop="target">
                            <el-autocomplete
                            class="inline-input"
                            v-model="RunningForm.target"
                            :fetch-suggestions="querySearch"
                            placeholder=""
                            @select="handleSelect"
                            ></el-autocomplete>
                            <!-- <el-input v-model="RunningForm.target" placeholder=""></el-input> -->
                        </el-form-item>
                        <el-form-item label="" prop="num">
                            <el-input-number v-model="RunningForm.num" :step="0.5"></el-input-number>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="onSubmit"></el-button>
                        </el-form-item>
                    </el-form>
                </template>
                <template v-else>
                    <el-table
                    height="300"
                    :data="resultTable"
                    style="width: 100%">
                        <el-table-column
                            prop="place"
                            label=""
                            width="250">
                        </el-table-column>
                        <el-table-column
                            prop="signUp"
                            label=""
                            width="110">
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </el-card>
    </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
//import
components: {},
data() {
//
return {
    selectList:[],

    title:'',
    isSubmit:false,
    RunningForm:{
        origin:'',
        target:'',
        num:1.5
    },

    resultTable:[
        {
            place:'1',
            signUp:''
        },
        {
            place:'2',
            signUp:''
        },
        {
            place:'3',
            signUp:''
        },
        {
            place:'4',
            signUp:''
        },
        {
            place:'5',
            signUp:''
        },
    ]
};
},
// data
computed: {},
//data
watch: {},
//
methods: {
    onShow(){
        this.refresh();
        document.getElementById('running-helper').style.display = 'block';
    },
    onClose(){
        this.title = ''
        this.isSubmit = false;
        document.getElementById('running-helper').style.display = 'none';
    },
    onSubmit(){
        this.title = ''
        this.isSubmit = true;
    },
    refresh(){
        this.RunningForm.origin = '',
        this.RunningForm.target = '',
        this.RunningForm.num = 1.5
    },

    querySearch(queryString, cb) {
        var list = this.selectList;
        var results = queryString ? list.filter(this.createFilter(queryString)) : list;
        //  callback 
        cb(results);
    },
    createFilter(queryString) {
        return (list) => {
          return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    loadAll(){
        // console.log(this.$store.state.placeList)
        return this.$store.state.placeList
    },

    handleSelect(item){
        console.log(item)
    }
},
// - this
created() {

},
// - DOM
mounted() {
    this.selectList = this.loadAll();
},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 400px;
    }


</style>


