<!--  -->
<template>
    <div id='date-log__container' style="position:absolute;display: none;
        left: 73%;top: 1%; z-index: 100;">
        <div class="date-log__card">
            <el-card class="box-card" shadow="always">
                <div slot="header" class="clearfix">
                    <span>{{title}}</span>
                    <el-button style="float: right; padding: 3px 0" type="text" @click="onClose"></el-button>
                </div>
                <div class="text item">
                    <template v-if="!isCalculate">
                        <el-form ref="Dateform" :model="DateLog" label-width="0px" :disabled="DateLog.isDisabled">
                            <el-form-item>
                                <el-input v-model="DateLog.name" :placeholder="DateLog.nameText" style="width:54%">
                                    <el-select v-model="DateLog.select" slot="prepend" placeholder="" style="width:90px;">
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                    </el-select>
                                </el-input>
                                <el-autocomplete
                                class="inline-input"
                                v-model="DateLog.place"
                                :fetch-suggestions="querySearch"
                                :placeholder="DateLog.placeText"
                                @select="handleSelect"
                                ></el-autocomplete>
                                <!-- <el-input v-model="DateLog.place" :placeholder="DateLog.placeText" style="width:45%"></el-input> -->
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="addToList">{{DateLog.addText}}</el-button>
                                <el-popover
                                    placement="top"
                                    width="160"
                                    v-model="isImport">
                                    <el-form label-width="0px">
                                        <el-form-item>
                                            <el-input v-model="Corse.account" placeholder=""></el-input>
                                        </el-form-item>
                                        <el-form-item>
                                            <el-input v-model="Corse.password" placeholder="" :show-password="Corse.isShowPassword"></el-input>
                                        </el-form-item>
                                        <el-form-item>
                                            <el-button type="primary" @click="importNow"></el-button>
                                            <el-button type="primary" @click="importCancel"></el-button>
                                        </el-form-item>
                                    </el-form>
                                    <el-button type="primary" slot="reference" style="margin-left:0;">{{DateLog.importText}}</el-button>
                                </el-popover>
                            </el-form-item>
                            <el-form-item>
                                <el-table
                                    :data="DateLog.targetList"
                                    style="width: 100%"
                                    height="300">
                                    <el-table-column
                                        label=""
                                        width="165">
                                        <template slot-scope="scope">
                                            <span style="margin-left: 10px">{{ scope.row.name }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column
                                        label=""
                                        width="140">
                                        <template slot-scope="scope">
                                            <span style="margin-left: 10px">{{ scope.row.place }}</span>
                                        </template>
                                    </el-table-column>
                                    <el-table-column label="">
                                        <template slot-scope="scope">
                                            <el-button
                                            size="mini"
                                            type="warning"
                                            @click="handleEdit(scope.$index, scope.row)"></el-button>
                                            <el-button
                                            size="mini"
                                            type="danger"
                                            @click="handleDelete(scope.$index, scope.row)"></el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="onSubmit"></el-button>
                            </el-form-item>
                        </el-form>
                    </template>
                    <template v-else>
                        <el-table
                            height="500"
                            :data="ResultTable"
                            style="width: 100%">
                            <el-table-column
                                prop="time"
                                label=""
                                width="180">
                            </el-table-column>
                            <el-table-column
                                prop="place"
                                label=""
                                width="180">
                            </el-table-column>
                        </el-table>
                    </template>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
let pointer = null;


export default {
//import
components: {},
data() {
    //
    return {
        selectList:[],

        isCalculate:false,

        title:"",

        // 
        currentIndex:-1,    
        // 
        DateLog:{
            isDisabled:false,
            name:'',
            place:'',
            select:'',
            nameText:"",
            placeText:"",

            addText:"",
            importText:'',

            targetList:[
                {
                    name:'1',
                    place:"1",
                    priority:2
                },{
                    name:'2',
                    place:"2",
                    priority:2
                },{
                    name:'3',
                    place:"3",
                    priority:2
                },{
                    name:'4',
                    place:"4",
                    priority:2
                },
            ]
        },

        isImport:false,

        // 
        Corse:{
            account:'',
            password:'',
            isShowPassword:false,
        },

        // 
        ResultTable:[
            {
                time:'',
                place:''
            }
        ],

        // json
        CorseJsonData:null,
    };
},
// data
computed: {},
//data
watch: {},
//
methods: {
    // 
    init(){
        pointer = this;
    },

    refresh(){
        this.$refs['Dateform'].resetFields();
    },

    // 
    onSubmit(){

    },

    // 
    importNow(){
        // 
        this.isImport = false;

        // json
        this.$message('...');
        this.DateLog.isDisabled = true;
        this.DateLog.importText = ""
        fetch('/static/Plugins/Json/corse.json').then(data=>data.json()).then(data=>{
            let res = []
            for(let i=0;i<data.data.length;i++){
                let temp = data.data[i].list;
                if(temp.length>0){
                    for(let j in temp){
                        res.push({
                            name:temp[j].name,
                            place:temp[j].place,
                            priority:temp[j].priority
                        })
                    }
                }
            }

            setTimeout(()=>{
                pointer.DateLog.targetList = res;

                pointer.DateLog.isDisabled = false;
                pointer.DateLog.importText = ""

                pointer.$message({
                    message: '!',
                    type: 'success'
                });
            },3000)
        })

    },

    importCancel(){
        // 
        this.isImport = false;
    },

    // 
    handleEdit(index,row){
        this.currentIndex = index;
        this.DateLog.addText = "";
        this.DateLog.name = row.name;
        this.DateLog.place = row.place;
        this.DateLog.select = row.priority == 2? '1':'2';

        console.log(index,row);
    },

    // 
    handleDelete(index){
        this.DateLog.targetList.splice(index, 1);
    },

    // 
    addToList(){
        if(this.currentIndex >= 0){
            this.DateLog.targetList[this.currentIndex].name = this.DateLog.name;
            this.DateLog.targetList[this.currentIndex].place = this.DateLog.place;
            this.DateLog.targetList[this.currentIndex].priority = this.DateLog.select == '1' ? 2 : 1;
            this.DateLog.name = '';
            this.DateLog.place = '';
            this.DateLog.select = '';
            this.currentIndex = -1;
            this.DateLog.addText = "";
            return;
        }
        if(this.DateLog.name != "" && this.DateLog.place != ""){
            this.DateLog.targetList.push({
                name:this.DateLog.name,
                place:this.DateLog.place,
                priority:this.DateLog.select == "" ? 2:1
            });
            this.DateLog.name = '';
            this.DateLog.place = '';
            this.DateLog.select = '';
        }
    },

    // 
    onShow(){
        this.refresh();
        document.getElementById('date-log__container').style.display = 'block'
    },

    // 
    onClose(){
        document.getElementById('date-log__container').style.display = 'none'
    },


    querySearch(queryString, cb) {
        var list = this.selectList;
        var results = queryString ? list.filter(this.createFilter(queryString)) : list;
        //  callback 
        cb(results);
    },
    createFilter(queryString) {
        return (list) => {
          return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    loadAll(){
        return this.$store.state.placeList
    },
    handleSelect(item){
        console.log(item)
    }
},
// - this
created() {
    this.init();
},
// - DOM
mounted() {
    this.selectList = this.loadAll();
},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style scoped>
    /*  */
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 16px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 500px;
    }
</style>

<style>
    ::-webkit-scrollbar {
        width: 5px;
        background-color: #fff;
    }
        
    ::-webkit-scrollbar-track {
        background-color: #fff;
    }
        
    ::-webkit-scrollbar-thumb {
        background: lightgray;
    }

    .el-table .cell {
        padding-left: 0px;
        padding-right: 0px;
    }
</style>


