import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    // 
    state:{
        auto_orient:false,
        placeList:[
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:'1'},
            {value:'2'},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''},
            {value:''}
        ]
    },

    // 
    actions:{
        changeLocationState(ctx,value){
            ctx.commit('changeLocationState',value)
        },
        changeCorse(ctx,data){
            ctx.commit('changeCorse',data);
        }
    },

    // 
    mutations:{
        changeLocationState(state,value){
            state.auto_orient = value;
        },
        changeCorse(state,data){
            state.Corse = data;
        }
    }
})


