<!--  -->
<template>
  <div class='container'>
    <el-drawer class="menuDrawer" title="" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'20%':'50%'">
      <div v-show="!isSearching">
        <el-form ref="runningForm" :model="RunRoute">
          <el-form-item class="routeInput">
            <el-autocomplete class="input" placeholder="" v-model="RunRoute.start" :fetch-suggestions="querySearch">
              <div slot="prepend"></div>
            </el-autocomplete>
            <el-autocomplete class="input" placeholder="" v-model="RunRoute.target" :fetch-suggestions="querySearch">
              <div slot="prepend"></div>
            </el-autocomplete>
          </el-form-item>
          <el-form-item>
            <el-input-number class="numInput" size="medium" v-model="RunRoute.length" :precision="1" :step="0.1" :min="1.5" :max="6"></el-input-number>
            <el-button class="button" type="primary" size="medium" @click="submitPoint()"></el-button>
          </el-form-item>
        </el-form>

        <el-image class="weatherImage" :src="weather.weatherUrl" fit="contain">
        </el-image>
        <el-descriptions title="" :column="2" class="weaInfo">
          <el-descriptions-item label="">{{weather.wea}}</el-descriptions-item>
          <el-descriptions-item label="">{{weather.air}}</el-descriptions-item>
          <el-descriptions-item label="">{{weather.temperature+""}}</el-descriptions-item>
          <el-descriptions-item label="">{{weather.humidity}}</el-descriptions-item>
        </el-descriptions>
      </div>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content=""></el-page-header>
        <el-descriptions class="margin-top" :column="2">
          <el-descriptions-item label="1">{{passList[0]}}</el-descriptions-item>
          <el-descriptions-item label="2">{{passList[1]}}</el-descriptions-item>
        </el-descriptions>
        <div class="buttonDiv">
          <span>{{""+length+""}}</span>
          <div class="btn">
            <el-button class="followBtn" type="primary" size="mini"></el-button>
            <el-button class="followBtn" type="primary" size="mini"></el-button>
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: ["drawer"],

  data() {
    //
    return {
      mydrawer: this.drawer,
      isSearching: false,
      RunRoute: {
        start: "",
        target: "",
        length: 1.5,
      },
      passList: ["", ""],
      length: 1.5,
      weather: {
        wea: "",
        temperature: 21,
        air: 0,
        humidity: "10%",
        weatherUrl: "../../static/Source/Images/qing.png",
      },
      selectList: [],
    };
  },
  // data
  computed: {},
  //watchdatawatch 
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.getWeather();
    this.selectList = this.loadAll();
  },
  // - 
  beforeCreate() {},
  // - 
  beforeMount() {},
  // - 
  beforeUpdate() {},
  // - 
  updated() {},
  // - 
  beforeDestroy() {},
  // - 
  destroyed() {},
  //keep-alive
  activated() {},
  //
  methods: {
    submitPoint() {
      if (this.RunRoute.start === "" || this.RunRoute.target === "") {
        return;
      }
      this.isSearching = true;
    },
    goBack() {
      this.isSearching = false;
    },
    getWeather() {
      var newWeather;
      this.$axios
        .get(
          "https://www.tianqiapi.com/free/day?appid=75698768&appsecret=7LyHELsc&city="
        )
        .then((reponse) => {
          console.log("", reponse.data);
          this.weather.temperature = reponse.data.tem;
          this.weather.air = reponse.data.tem;
          this.weather.humidity = reponse.data.humidity;
          this.weather.wea = reponse.data.wea;
          this.weather.weatherUrl =
            "../../static/Source/Images/" + reponse.data.wea_img + ".png";
        })
        .catch((err) => {
          console.log(err);
        });
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      console.log(this.selectList);
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
  overflow: hidden;
}

.routeInput .input {
  margin: 10px 10px;
  width: 90%;
}

.numInput {
  margin: 0 10px;
}

.button {
  margin-left: 50px;
}

.weatherImage {
  float: left;
  margin-left: 10px;
  margin-top: 10px;
  width: 40%;
  height: 100px;
}

.weaInfo {
  margin-top: 10px;
  float: right;
  width: 50%;
}

.getRoute {
  margin: 10px 10px;
}

.routeContent {
  position: relative;
  margin: 10px 20px;
}
.margin-top {
  margin: 10px 10px;
}
.buttonDiv {
  margin-left: 10px;
}
.btn {
  display: inline-block;
  position: absolute;
  right: 2%;
}
</style>


