<!--  -->
<template>
    <div style="width:1920px;height:932px;">
        <div class="nav-header">
            <div class="nav-header__title" @click="Home">
                <span style="font-size:40px; font-style: italic;"></span><br>
                <span style="font-size:18px;margin-left:35%"></span>
            </div>
            <div class="nav-header__avatar" @click="UserMenu">
                <div style="width: 64px;height: 64px;">
                    <el-avatar :size="Avatar.size" :src="Avatar.url"></el-avatar>
                </div>
            </div>
        </div>
        <div class="nav-body">
            <sys ref="SysSetting" />
            <routeplan ref="RoutePlan" />
            <runninghelper ref="RunningHelper" />
            <about ref="About" />
            <datelog ref="DateLog" />
            <div id="antvMap"></div>
            <div style="position: absolute; z-index: 10;">
                <el-menu default-active="" class="el-menu-vertical-demo" @select="MenuSelect" :collapse="isCollapse">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <span slot="title"></span>
                            <el-menu-item index="1-1"></el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="">
                            <el-menu-item index="1-2"></el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="">
                            <el-menu-item index="1-3"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="4">
                        <template slot="title">
                            <i class="el-icon-setting"></i>
                            <span slot="title"></span>
                        </template>
                        <el-menu-item-group>
                            <span slot="title"></span>
                            <el-menu-item index="4-1"></el-menu-item>
                            <el-menu-item index="4-2"></el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="">
                            <el-menu-item index="4-3"></el-menu-item>
                            <el-menu-item index="4-4"></el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-menu-item index="5">
                        <i class="el-icon-info"></i>
                        <span slot="title"></span>
                    </el-menu-item>
                </el-menu>
            </div>
        </div>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import { AntvMap } from '../../static/Plugins/Script/antvMap.js'
import About from '../components/About.vue'
import Sys from '../components/Sys.vue'
import Runninghelper from '../components/RunningHelper.vue'
import Routeplan from '../components/RoutePlan.vue'
import Datelog from '../components/DateLog.vue'

export default {
    //import
    components: {
        Sys,
        About,
        Runninghelper,
        Routeplan,
        Datelog
    },
    data() {
    //
        return {
            Avatar:{
                size:64,
                url:'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'
            },

            isCollapse:true,

            SysOPtions:{
                activeName:'',
                isAccordion:true
            },

            AntvMapInstance:null,
        };
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
        init(){
            this.AntvMapInstance = new AntvMap( 'antvMap' );
            this.AntvMapInstance.loadMap();
        },
        // loadMap(){
        //     console.log("");
        //     console.log(navigator.userAgent.match('Win')? 'windows' : '')
        //     LoadMap('antvMap');
        // },

        // 
        Home(){
            this.isCollapse = !this.isCollapse;
        },

        // 
        UserMenu(){

        },

        MenuSelect(index){
            let options = '';
            switch(index){
                case '1-1':
                    options = 'RoutePlan'
                    break;
                case '1-2':
                    options = 'RunningHelper'
                    break;
                case '1-3':
                    options = 'DateLog'
                    break;
                case '4-3':
                    options = 'SysSetting'
                    break;
                case '5':
                    options = 'About'
                    break;
                default:
                    this.$message('~');
                    return;
            }
            this.$refs[options].onShow();
        }
        
    },
    // - this
    created() {

    },
    // - DOM
    mounted() {
        // this.loadMap();
        this.init();
    },
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
}
</script>
<style scoped>
    .nav-body{
        position: relative;
        width: 100%;
        height: 91.4%;
    }
    #antvMap{
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .nav-header{
        position: relative;
        width:100%;
        height: 8.6%;
        background-color: #409EFF;
        z-index: 10;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .nav-header__title{
        display: inline-block;
        width: 200px;
        margin-left: -88%;
    }
    .nav-header__title span{
        color: white;
        font-family: 'common-Regular';
    }
    .nav-header__title span,
    .nav-header__avatar:hover{
        cursor: pointer;
    }

    .nav-header__avatar{
        display: inline-block;
        position: absolute;
        right: 1%;
    }

    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 851px;
    }

</style>

<style>
    .el-collapse-item__header{
        font-size:18px !important;
        padding-left: 10%;
    }
    .el-drawer__header span{
        font-size: 20px;
    }
</style>


