<!--  -->
<template>
  <div class="container">
    <el-drawer class="menuDrawer" title="" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'20%':'30%'">
      <el-form ref="form" :role="roles" :model="navigatorForm" v-show="!isSearching">
        <div class="routerInput">
          <el-form-item prop="origin">
            <el-autocomplete v-model="navigatorForm.origin" :placeholder="originText" clearable :fetch-suggestions="querySearch">
              <div class="title" slot="prepend"></div>
            </el-autocomplete>
          </el-form-item>
          <el-form-item prop="target">
            <el-autocomplete v-model="navigatorForm.target" :placeholder="targetText" clearable :fetch-suggestions="querySearch">
              <div class="title" slot="prepend"></div>
            </el-autocomplete>
          </el-form-item>
        </div>
        <div class="routeModel">
          <el-form-item class="model">
            <el-switch v-model="navigatorForm.isWalk" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text="">
            </el-switch>
            <el-button class="startNav" type="primary" @click="searchRoute()"></el-button>
          </el-form-item>
        </div>
      </el-form>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content=""></el-page-header>
        <el-descriptions class="margin-top" :column="2">
          <el-descriptions-item label="">{{navigatorForm.origin}}</el-descriptions-item>
          <el-descriptions-item label="">{{navigatorForm.target}}</el-descriptions-item>
        </el-descriptions>
        <div class="carBtn">
          <el-switch class="swt" v-show="!navigatorForm.isWalk" v-model="navigatorForm.isSignUp" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text="">
          </el-switch>
          <el-switch class="swt" v-show="!navigatorForm.isWalk" v-model="navigatorForm.isParkNeed" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text="">
          </el-switch>
        </div>
        <div class="buttonDiv">
          <span>{{""+length+""}}</span>
          <div class="btn">
            <el-button class="followBtn" type="primary" size="mini"></el-button>
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: ["drawer"],
  data() {
    //
    return {
      mydrawer: this.drawer,
      isSearching: false,

      originText: "",
      targetText: "",

      navigatorForm: {
        origin: "",
        target: "",
        isWalk: true,
        isSignUp: true,
        isParkNeed: true,
      },
      passList: [],
      length: 1.5,
      selectList: [],

      roles: {
        origin: [{ required: true, message: "", trigger: "blur" }],
        target: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  // data
  computed: {},
  //watchdatawatch 
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    console.log("jinlaile");
    this.selectList = this.loadAll();
  },
  // - 
  beforeCreate() {},
  // - 
  beforeMount() {},
  // - 
  beforeUpdate() {},
  // - 
  updated() {},
  // - 
  beforeDestroy() {},
  // - 
  destroyed() {},
  //keep-alive
  activated() {},
  //
  methods: {
    searchRoute() {
      if (
        this.navigatorForm.origin === "" ||
        this.navigatorForm.target === ""
      ) {
        return;
      }
      this.isSearching = true;
    },
    goBack() {
      this.isSearching = false;
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      console.log(this.selectList);
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
}

.routerInput {
  padding: 5px 20px;
  padding-top: 15px;
}

.startNav {
  position: absolute;
  right: 10px;
}

.swt {
  margin-left: 10px;
  margin-bottom: 10px;
}

.routeModel .model {
  font-size: 14px;
  margin: 0 20px;
}

.el-drawer__body {
  border-radius: 10px;
}
.getRoute {
  margin: 10px 10px;
}

.routeContent {
  position: relative;
  margin: 10px 20px;
}
.margin-top {
  margin: 10px 10px;
}
.buttonDiv {
  margin-left: 10px;
}
.btn {
  float: right;
}
</style>


